@import "~react-toolbox/lib/_colors"; 
 $theme-building: true;
$color-primary: $palette-blue-500 !default;
$color-primary-dark: $palette-blue-800 !default;
$color-primary-light: $palette-blue-300 !default;

$color-accent: $palette-blue-a200 !default;
$color-accent-dark: $palette-blue-700 !default;

$color-content: #fafafa;

@import "../base";
@import "./config";

.root {
  position: fixed;
  top: 0;
  left: 0;
  z-index: $z-index-highest;
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  &.invisible > *:not(.overlay) {
    pointer-events: all;
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $overlay-color;
  opacity: 0;
  transition-timing-function: $animation-curve-default;
  transition-duration: $animation-duration;
  transition-property: opacity;
}

.active {
  pointer-events: all;
  > .overlay {
    opacity: $overlay-opacity;
  }
}



