@import "~react-toolbox/lib/_colors"; 
 $theme-building: true;
$color-primary: $palette-blue-500 !default;
$color-primary-dark: $palette-blue-800 !default;
$color-primary-light: $palette-blue-300 !default;

$color-accent: $palette-blue-a200 !default;
$color-accent-dark: $palette-blue-700 !default;

$color-content: #fafafa;

@import "../base";
@import "./config";

.root {
  display: flex;
  max-width: 96vw;
  max-height: 96vh;
  flex-direction: column;
  background-color: $dialog-color-white;
  border-radius: $dialog-border-radius;
  box-shadow: $zdepth-shadow-5;
  opacity: 0;
  transition-delay: $animation-delay;
  transition-timing-function: $animation-curve-default;
  transition-duration: $animation-duration;
  transition-property: opacity, transform;
  transform: translateY(-$dialog-translate-y);
  &.active {
    opacity: 1;
    transform: translateY(0%);
  }
}

.small {
  width: 30vw;
}

.normal {
  width: 50vw;
}

.large {
  width: 96vw;
}

.title {
  @include typo-title();
  flex-grow: 0;
  margin-bottom: 1.6 * $unit;
  color: $dialog-color-title;
}

.body {
  flex-grow: 2;
  padding: $dialog-content-padding;
  overflow-y: auto;
  color: $color-text-secondary;
}

.navigation {
  flex-grow: 0;
  padding: $dialog-navigation-padding;
  text-align: right;
}

.button {
  min-width: 0;
  padding-right: $dialog-navigation-padding;
  padding-left: $dialog-navigation-padding;
  margin-left: $dialog-navigation-padding;
}



